.advantages {
    background-color: $bgcolor-main;
    padding-bottom: 64px;
    
    @include tablet {
        padding-bottom: 204px;
        padding-top: 110px;
    }
    @include desktop {
        padding-bottom: 178px;
    }
}

.advantages__list { 
    
    @include tablet {
        display: flex;
        flex-wrap: wrap;
        gap: 80px;
    }
    @include desktop {
        gap: 32px;
    }
}
.advantages__item {
    display: flex;
    flex-direction: column;
    @extend %flex-center;
    width: 100%;
    
    &:not(:last-child) {
        margin-bottom: 25px;
    }
    &:nth-child(1)::before {
        background-image: url(../images/advantages/cow.png);
    }
    &:nth-child(2)::before {
        background-image: url(../images/advantages/weight.png);
    }
    &:nth-child(3)::before {
        background-image: url(../images/advantages/ice-crema.png);
    }
    &::before {
            content: "";
            background-color: $main-color;
            background-repeat: no-repeat;
            background-position: center;
            width: 50px;
            height: 50px;
            border-radius: 25px;
            margin-bottom: 16px;
        }

    @include tablet {
        display: flex;
        flex-basis: calc((100% - 160px) / 3);
        width: 152px;
        height: auto;
        position: relative;
        margin: 0;
        padding-top: 32px;
        
        @extend %flex-center;
        &:not(:last-child) {
            margin-bottom: 0;
        }
        &::before  {
            position: absolute;
            top: -32px;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    @include desktop {
        width: 368px;
        flex-basis: calc((100% - 64px) / 3);
        padding-top: 40;
        &::before {
            position: absolute;
            top: -40px;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}
.advantages__subtitle {
    font-weight: 700;
        font-size: 36px;
        line-height: calc(44/36);
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
    
        color: $text-reverse-color;
        margin-bottom: 16px;

        @include tablet {
            font-size: 48px;
            line-height: calc(59/48);
            margin-bottom: 12px;
        }
        @include desktop {
            font-size: 58px;
            line-height: calc(71/58);
        }
        
}
.advantages__text {
    width: 100%;
    font-weight: 700;
        font-size: 12px;
        line-height: 170%;
        text-align: center;
    
        color: $text-reverse-color;

        @include tablet {
            font-weight: 400;
            font-size: 14px;
            line-height: 174%;
        }
        @include desktop {
            font-weight: 400;
            font-size: 16px;
            line-height: 176%;
        }
}